//
//    WRAPPER
//    контейнер страницы, прижатие футера


.wrapper {
	margin: 0px auto;
	min-width: 300px;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	box-shadow: 0px 0px 5px rgba( 0, 0, 0, 0.25 );
	position: relative;

	&__item--main {
		flex: 1;
		position: relative;
		overflow: hidden;
	}


	&__item--header,
	&__item--footer {
		padding: 1em 0;
		background-color: $brand-primary;
		color: #fff;
	}


}